.certDetail {
  &-img {
    padding: .5em;
    img {
      width: 4em;
      display: block;
      margin: 0 auto;
      border-radius: 20%;
      background-color: white;
    }
  }

  &-techList {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  &-coursesList {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  &-info {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
  }

  &-infoItem {
    flex: 1fr;
    padding: .5em;
  }

  section {
    padding: .3em;
    font-size: .8em;

    h3 {
      font-weight: bold;
      text-align: center;
      padding: .5em;
    }

    h4 {
      font-weight: bold;
      margin-bottom: .2em;
      text-align: center;
    }

  }
}
